* {
  box-sizing: border-box;
}
html,body{
  height: 100vh;
}
body {
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: center;
  background-color: rgb(238, 238, 238);
  align-items: center;
}
.box {
  padding: 3.125rem;
  border-radius: 1.125rem;
  background-color: white;
  max-width: 700px;
  max-height: 500px;
  border-bottom-right-radius: 12.5rem;
}
p {
  font-weight: 400;
  color: grey;
  margin: 0;
}
.main-input {
  font-weight: 800;
  padding: 0 1.25rem;
  font-size: 1.5rem;
  border: 2px solid rgb(231, 231, 231);
  border-radius: 0.313rem;
  height: 3.75rem;
  width: 100%;
  color: black;
}
.inline {
  margin-right: 2rem;
  max-width: 8rem;
}

input:focus {
  outline-color: rgb(155, 48, 255);
}

.top {
  display: flex;
  width: 80vw;
  height: 6.25rem;
}
.line {
  width: 90%;
  margin-top: 1.875rem;
  border-top: 2px solid rgb(231, 231, 231);
}

.circle {
  margin-top: -0.625rem;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  overflow: hidden;
  background-color: black;
}

img {
  user-select: none;
  height: 2.5rem;
  width: 2.5rem;
}
.mid {
  display: flex;
}
.circle:active {
  background-color: rgb(155, 48, 255);
}
.age {
  font-weight: 800;
  font-style: italic;
  line-height: 5rem;
  font-size: 4.375rem;
  color: black;
}
span {
  color: rgb(155, 48, 255);
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield; /* Firefox */
}
.error-log {
  margin-top: 0.313rem;
  font-size: 0.75rem;
  color: rgb(255, 126, 126);
}
@media (max-width: 600px) {
  .box {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    min-height: 70vh;
    width: 95vw;
    border-radius: 1rem;
    padding: 4rem 2rem;
    border-bottom-right-radius:10rem;
  }
 

  html {
    font-size: 12px;
  }
  .top {
    margin-bottom: 2rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .line {
    width: 100%;
  }
  .mid {
    height: 5rem;
  }
  .circle {
    right: calc(50% - 30px);
    position: absolute;
  }
  .inline {
    width: 30%;
    margin-right: 0;
  }
}
